import md5 from '@/md5.js'
export default {
	data() {
		return {
			swiper_list: [],
			truelist: [],
			newlist: [],
			classiclist: [],
			userInfo: {}
		};
	},
	onLoad: function() {
		this.getlist();
		this.getbanners();
		this.userInfo = this.$store.state;
	},
	methods: {
		getapi() {
			var timestamp = Date.parse(new Date());
			var api_key = this.api_key;
			return [timestamp, md5(api_key + timestamp)];
			//返回api_key+时间戳md5加密
		},
		async getlist() {
			var key = await this.getapi();
			uni.request({
				url: this.url_config+'app/books/getNewest',
				data: {
					time: key[0],
					token: key[1]
				},
				success: (res) => {
					this.truelist = res.data.newest;
					this.newlist = this.truelist.sort(function() {
						return .5 - Math.random();
					}).slice(0, 4);
					this.classiclist = this.truelist.sort(function() {
						return .5 - Math.random();
					}).slice(0, 6);
				}
			});
		},
		async getbanners() {
			var key = await this.getapi();
			uni.request({
				url: this.url_config+'app/tags/getBanners',
				data: {
					time: key[0],
					token: key[1],
					num: 3
				},
				success: (res) => {
					this.swiper_list = res.data.banners;
					console.log(res.data)
				}
			});
		},
		changenewlist() {
			this.newlist = this.truelist.sort(function() {
				return .5 - Math.random();
			}).slice(0, 4);
		},
		changeclassiclist() {
			this.classiclist = this.truelist.sort(function() {
				return .5 - Math.random();
			}).slice(0, 6);
		},
		gotoupdate() {
			uni.navigateTo({
				url: '../updatelist/updatelist'
			})
		},
		gotoclassic() {
			uni.navigateTo({
				url: '../classiclist/classiclist'
			})
		},
		gotohot() {
			uni.navigateTo({
				url: '../hotlist/hotlist'
			})
		},
		gotoclassification() {
			uni.navigateTo({
				url: '../classification/classification'
			})
		},
		gotocomicdetails(id, uid) {
			uni.navigateTo({
				url: '../comicdetails/comicdetails?id=' + id + '&uid=' + uid
			})
		},
		gotosearch() {
			uni.navigateTo({
				url: '../search/search'
			})
		},
		clickswiper(index) {
			console.log(index);
			this.gotocomicdetails(this.swiper_list[index].book_id, this.userInfo.uid)
		}

	}
}
